.header {
    border-bottom: 1px solid #EDEDED;
    font-family: Roboto, sans-serif;

    @media (max-width: 767px) {
        padding: 20px 0;
    }
}
.header__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.header__nav {

    @media (max-width: 767px) {
        position: absolute;
        top: 0;
        left: 0;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100%;
        background: #fff;
        z-index: 3;
        transition: 0.4s ease-in-out;
        transform: translateY(-100%);

        &.active {
            transform: none;
        }
    }
}

.header__list {
    display: flex;

    @media (max-width: 767px) {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
}

.header__list_item {
    margin-left: 60px;

    @media (max-width: 767px) {
        margin-left: 0;
    }

    a {
        position: relative;
        display: block;
        padding: 23px 0;
        font-size: 14px;

        @media (max-width: 767px) {
            font-size: 16px;
            margin-bottom: 10px;
        }

        &.active {

            &::before {
                content:'';
                position: absolute;
                bottom: 0;
                height: 5px;
                width: 100%;
                background: #5A6BFF;
            }
        }
    }

    &:last-child {
        margin-right: 0;
    }
}

.menu_toggle {
    position: relative;
    z-index: 3;
    flex-shrink: 0;
    display: none;
    width: 54px;
    height: 54px;
    padding: 0 11px;
    border: 2px solid #B3B1C2;
    transition: .5s ease-in-out;
    cursor: pointer;

    @media (max-width: 767px) {
        display: block;
        width: 32px;
        height: 32px;
        padding: 0 6px;
    }

    &.open {

        span {
            &:first-child {
                width: 0;                
            }
    
            &:nth-child(2) {
                transform: rotate(45deg);
            }
    
            &:nth-child(3) {
                transform: rotate(-45deg);
            }
            &:nth-child(4) {
                width: 0;
            }
        }
    }

    span {
        display: block;
        position: absolute;
        height: 2px;
        width: calc(100% - 22px);
        background: #B3B1C2;
        opacity: 1;
        left:11px;
        transform: rotate(0);
        transition: .25s ease-in-out;

        @media (max-width: 1023px) {
            width: calc(100% - 12px);
            left: 6px;
        }

        &:first-child {
            top: 17px;

            @media (max-width: 1023px) {
                top: 12px;
            }

            @media (max-width: 767px) {
                top: 10px;
            }
        }

        &:nth-child(2) {
            top: 26px;
            
            @media (max-width: 1023px) {
                top: 18px;
            }
            
            @media (max-width: 767px) {
                top: 14px;
            }
        }
        &:nth-child(3) {
            top: 26px;

            @media (max-width: 1023px) {
                top: 18px;
            }
            @media (max-width: 767px) {
                top: 14px;
            }
        }

        &:nth-child(4) {
            top: 35px;

            @media (max-width: 1023px) {
                top: 24px;
            }
            @media (max-width: 767px) {
                top: 18px;
            }
        }
    }
}