.footer {
    padding: 25px 0;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
}

.footer__content {
    display: flex;
    justify-content: space-between;
    align-items: center;

    @media (max-width: 767px) {
        display: block;
    }
}

.footer__links {
    @media (max-width: 1023px) {
        display: flex;
        flex-direction: column;
    }

    @media (max-width: 767px) {
        margin-top: 20px;
    }
}

.footer__link {
    margin-right: 62px;
    @media (max-width: 1023px) {
        display: block;
        margin-bottom: 10px;
    }

    &:last-child {
        margin-right: 0;
        margin-bottom: 0;
    }
}