.event_item {
    padding-top: 29px;
}

.event_item__block {
    display: flex;
    justify-content: space-between;

    @media (max-width: 1023px) {
        flex-direction: column;
        align-items: center;
    }
}

.event_item__top {
    display: flex;
    align-items: center;
    margin-bottom: 24px;

    .section_title {
        margin-left: 23px;
    }
}

.event_item__info {
    flex-grow: 1;

    .event__card {
        margin-bottom: 16px;
        padding: 0 0 16px 0;
        border: 0;
        border-radius: 0;
        border-bottom: 1px solid #F2F2F2;
    }
}