$baseColor: #000000;
$color2: #5F47F0;
$color2Light: #E1E4FF;

@font-face {
    font-family: Roboto;
    src: url(./fonts/Roboto/Roboto-Regular.woff);
    font-style: normal;
    font-weight:400;
    font-display: swap;
}
@font-face {
    font-family: Roboto;
    src: url(./fonts/Roboto/Roboto-Medium.woff);
    font-style: normal;
    font-weight:500;
    font-display: swap;
 }
@font-face {
    font-family: Inter;
    src: url(./fonts/Inter/Inter-Bold.woff);
    font-style: normal;
    font-weight:700;
    font-display: swap;
}
@font-face {
    font-family:Inter;
    src: url(./fonts/Inter/Inter-Black.woff);
    font-style: normal;
    font-weight:800;
    font-display: swap;
 }
@font-face {
    font-family: SF Pro Text;
    src: url(./fonts/SFProText/SFProText-Bold.woff);
    font-style: normal;
    font-weight:700;
    font-display: swap;
}
@font-face {
    font-family: SF Pro Text;
    src: url(./fonts/SFProText/SFProText-Medium.woff);
    font-style: normal;
    font-weight:500;
    font-display: swap;
 }
@font-face {
    font-family: SF Pro Text;
    src: url(./fonts/SFProText/SFProText-Regular.woff);
    font-style: normal;
    font-weight:400;
    font-display: swap;
 }
@font-face {
    font-family: SF Pro Text;
    src: url(./fonts/SFProText/SFProText-Light.woff);
    font-style: normal;
    font-weight:300;
    font-display: swap;
 }

* {
    min-height: 0;
    min-width: 0;
    box-sizing: border-box;
}

html {
    // scroll-behavior: smooth;
}

html,
body {
    min-height: 100%;
}
body {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    font-family: "SF Pro Text", sans-serif;
    font-size: 16px;
    line-height: normal;
    margin: 0;
    background: #fff;
    color: $baseColor;

    // @media (max-width: 767px) {
    // 	overflow: hidden;
    // }
}
main {
    flex-grow: 1;
}

a {
    text-decoration: none;
    color: inherit;
    cursor: pointer;
}

h1,
h2,
h3,
h4,
p,
ul {
    margin: 0;
    padding: 0;
    font-weight: 400;
}

li {
    list-style: none;
}

.no_scroll {
    overflow: hidden;
}

input {
    outline: none;

    &:required {
        box-shadow: none;
    }
}
input[type="submit"] {
    font-family: "Roboto", sans-serif;
    cursor: pointer;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
    -webkit-appearance: none;
    margin: 0;
}

input[type="number"] {
    -moz-appearance: textfield;
}

.input {
    width: 100%;
    height: 48px;
    border: 0;
    padding: 0 20px;
    transition: 0.2s ease-in-out;    
    background: #f9f9f9;
    border-radius: 24px;
}


.input {
}

.hidden {
    display: none !important;
}

button {
    cursor: pointer;
    border: 0;
    padding: 0;
    font-weight: 5 00;
    font-size: 16px;
    transition: 0.2s ease-out;
    background: none;
    outline: none;

    &:disabled {
        cursor: auto;
    }
}

.btn {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: 0.2s ease-in-out;
    font-weight: bold;
    background: #FFFFFF;
}

.btn_1 {
    border: 1px solid #EDEDED;  
    border-radius: 35px;
    font-family: Inter;
    font-size: 12px;
    line-height: 16px;
    height: 48px;
    padding: 0 31px;
}

.btn_2 {
    padding: 15px 32px;
    border: 1px solid #5A6BFF;
    border-radius: 62px;
    font-weight: 500;
    font-size: 20px;
    line-height: 150.6%;
    color: #5A6BFF;
    letter-spacing: 1px;

    @media (max-width: 767px) {
        padding: 10px 15px;
        font-size: 16px;
    }
}


img {
    display: block;
}

.semiBold {
    font-weight: 500;
}

.bold {
    font-weight: 700;
}

.container {
    width: 100%;
    max-width: 1320px;
    margin: 0 auto;
    padding: 0 20px;

    @media (max-width: 767px) {
        padding: 0 10px;
    }
}

.section_title {
    font-family: Inter;
    font-weight: 800;
    font-size: 50px;
    line-height: 61px;

    &.section_title--small {
        font-size: 32px;
        line-height: 39px;

        @media (max-width: 767px) {
            font-size: 24px;
            line-height: 32px;
        }
    }

    @media (max-width: 1023px) {
        font-size: 36px;
        line-height: 42px;
    }

    @media (max-width: 767px) {
        font-size: 24px;
        line-height: 32px;
    }
}

.success {
    color: #38CC7D;
}

.subtitle {
    margin-bottom: 8px;
    font-size: 12px;
    line-height: 16px;
    color: #808080;
}

.tag__list {
    display: flex;
    flex-wrap: wrap;
}

.tag {
    margin-bottom: 8px;
    margin-right: 8px;    
    font-size: 10px;
    line-height: 16px;
    font-weight: 700;
    text-transform: uppercase;

    &.tag--primary {
        padding: 4px 12px;
        background: #F5F5F5;
        border-radius: 12px;
        color: #303030;
    }

    &.tag--secondary {
        padding: 8px 16px;
        background: #EFEDFD;
        border-radius: 16px;
        color: #5F47F0;
    }
}

.label {
    position: absolute;
    top: 18px;
    left: 14px;
    z-index: 2;
    display: flex;
    align-items: center;
    padding: 4px 8px;
    border-radius: 16px;
    color: #FFFFFF;
    font-weight: 500;
    font-size: 11px;

    &.label--primary {
        background: #5F47F0;

        &::before {
            content: '';
            display: block;
            flex-shrink: 0;
            width: 12px;
            height: 12px;
            background: url(/img/design/checked.svg) center no-repeat;
            margin-right: 10px;
        }
    }
}

body .swiper-container-horizontal > .swiper-pagination-bullets {
    bottom: 18px;

    .swiper-pagination-bullet {
        margin: 0 5px;
    }
} 

body .swiper-pagination-bullet {
    width: 10px;
    height: 10px;
    background: #A9ABC9;
    opacity: 1;

    &.swiper-pagination-bullet-active {
        background: #5F47F0;
    }
}