.event__map {
    position: absolute;
    top: 0;
    right: 0;
    width: 48.88vw;

    @media (min-width: 1441px) {
        width: 49.2vw;
    }

    @media (max-width: 1023px) {
        position: relative;
        width: 100%;
        margin-top: 20px;
    }

    iframe {
        width: 100%;
        height: 664px;

        @media (max-width: 767px) {
            height: 300px;
        }
    }
}