.event__card {
    margin-bottom: 24px;
    padding: 16px;
    background: #FFFFFF;
    border: 1px solid #EDEDED;
    border-radius: 16px;

    &:last-child {
        margin-bottom: 0;
    }
}

.event__top {
    display: flex;
    justify-content: space-between;

    .event__left {
        display: flex;
        align-items: center;
    }

    .event__image {
        margin-right: 12px;
    }

    .event__title {
        font-weight: 700;
        font-size: 18px;
        line-height: 24px;
    }

    .event__address {
        font-size: 12px;
        line-height: 16px;
        
        .highlight {
            color:#5F47F0;
        }
    }

    .event__rating {
        display: flex;
        align-items: center;
        margin-top: 8px;
        font-size: 15px;
        line-height: 12px;

        &::before {
            content: '';
            display: block;
            flex-shrink: 0;
            width: 14px;
            height: 14px;
            margin-right: 6px;
            background: url(/img/design/star.svg) center no-repeat;
        }
    }
}

.event__main {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    padding-bottom: 16px;
    border-bottom: 1px solid  #F2F2F2;

    @media (max-width: 767px) {
        display: block;
        padding-top: 20px;
    }
}

.event__shedule {
    display: flex;
    align-items: center;

    .title {
        display: block;
        margin-bottom: 11px;
        font-weight: 300;
        font-size: 10px;
        line-height: 12px;
        color: #808080;
    }

    .event__data {
        display: flex;
        align-items: center;
        margin-bottom: 11px;
        font-size: 15px;
        line-height: 12px;

        &::before {
            content: '';
            display: block;
            width: 16px;
            height: 16px;
            margin-right: 7px;
            background: url(/img/design/calendar.svg) center no-repeat;
        }
    }

    .event__time {
        display: flex;
        align-items: center;
        font-size: 15px;
        line-height: 12px;

        &::before {
            content: '';
            display: block;
            width: 16px;
            height: 16px;
            margin-right: 7px;
            background: url(/img/design/time.svg) center no-repeat;
        }
    }

    .event__separate {
        width: 24px;
        height: 24px;
        margin: 0 40px;
        background: url(/img/design/arrows.svg) center no-repeat;

        @media (max-width: 767px) {
            margin: 0 20px;
        }
    }
}

.event__members {

    @media (max-width: 767px) {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
        margin-top: 20px;
    }
    .event__counter {
        font-size: 17px;
        line-height: 18px;
        text-align: right;
    }

    .title {
        display: block;
        font-size: 10px;
        line-height: 12px;
        text-align: right;
    }

    .event__avatars {
        display: flex;
        margin-top: 10px;
    }

    .event__avatar {
        width: 44px;
        height: 44px;
        border-radius: 50%;
        margin-left: -22px;
    }

    .event__avatar--more {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 44px;
        height: 44px;
        border-radius: 50%;
        background: #5F47F0;
        font-weight: 600;
        font-size: 12px;
        line-height: 14px;
        color: #fff;
        padding-left: 4px;
    }
}

.event__bottom {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 16px;

    .event__enter {
        font-weight: 300;
        font-size: 10px;
        line-height: 12px;
        color: #808080;
    }

    .event__type {
        font-size: 15px;
        line-height: 12px;

        &.event__type--price {
            font-weight: 800;

            .currency {
                font-weight: 400;
                color:#5F47F0;
            }
        }
    }
}