.event__details {
    padding-bottom: 25px;
}

.event__categories {
    display: grid;
    grid-template-columns: 180px 180px;
    justify-content: space-between;
    align-items: center;
    gap: 32px 0;
    margin-bottom: 28px;

    @media (max-width: 767px) {
        display: block;
    }

    .subtitle {
        margin-bottom: 4px;
        font-size: 12px;
        line-height: 16px;
        color: #808080;
    }

    .event__value {
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
    }
}

.event__category {
    @media (max-width: 767px) {
        margin-bottom: 20px;
    }
}

.event__creator {
    display: flex;
    align-items: center;

    .event__avatar {
        width: 44px;
        height: 44px;
        border-radius: 50%;
        margin-right: 16px;
    }

    .subtitle {
        margin-bottom: 0;
        font-weight: 300;
        font-size: 10px;
        line-height: 12px;
    }

    .event__name {
        font-weight: 500;
        font-size: 14px;
        line-height: 20px;
    }
}

.event__tags {
    margin-bottom: 26px;
}

.event__description {
    margin-bottom: 32px;
}

.description__text {
    font-size: 14px;
    line-height: 20px;
}

.event__demands {
    margin-bottom: 29px;
}

.event__go_to {
    @media (max-width: 767px) {
        width: 100%;
    }
}