.banner {
    position: relative;

    &.banner--first {
        background: #FAFAFA;
        padding: 204px 0;

        @media (max-width: 1023px) {
            padding: 60px 0;
        }

        .banner__img {
            position: absolute;
            top: 50%;
            right: 0;
            transform: translateY(-50%);

            @media (max-width: 1260px) {
                width: 450px;
            }

            @media (max-width: 1023px) {
                position: static;
                margin-left: auto;
                margin-right: -20px;
                transform: none;
            }

            @media (max-width: 767px) {
                width: 100%;
                margin-right: -10px;
            }
        }
    }

    &.banner--second {
        background: #E1E4FF;

        .banner__content {
            display: flex;
            justify-content: flex-end;
            height: 100%;
        }

        .banner__img {
            max-width: 100%;
        }
    }

    &.banner--third {
        background: #E1E4FF;
        padding: 204px 0;

        @media (max-width: 1023px) {
            padding: 60px 0 0 0;
        }

        .banner__img {
            position: absolute;
            bottom: 0;
            right: 0;

            @media (max-width: 1260px) {
                width: 425px;
            }

            @media (max-width: 1023px) {
                position: static;
                margin-top: 40px;
                margin-left: auto;
                margin-right: -20px;
                transform: none;
            }

            @media (max-width: 767px) {
                width: 100%;
                margin-right: -10px;
            }
        }
    }

    .banner__img {
        max-width: 100%;

        @media (max-width: 767px) {
            width: 100%;
            margin-right: -10px;
        }
    }

    .section_title {
        margin-bottom: 8px;
    }
}

.banner__text {
    max-width: 570px;
}

.banner__description {
    margin-bottom: 67px;
    font-size: 18px;
    line-height: 21px;
    letter-spacing: -0.015em;
    color: #969696;
}

.banner__links {
    display: flex;
}

.banner__link {
    width: 140px;
    height: 40px;
    margin-right: 10px;

    &:last-child {
        margin-right: 0;
    }

    img {
        width: 100%;
        height: 100%;
    }
}