.event__gallery {
    position: relative;
    flex-shrink: 0;
    width: 48.7%;
    height: 542px;
    margin-right: 32px;
    border-radius: 5px;

    @media (max-width: 1023px) {
        width: 70%;
        height: auto;
        margin-right: 0;
        margin-bottom: 20px;
    }

    @media (max-width: 767px) {
        width: 100%;
    }
    
    img {
        height: 100%;
        object-fit: cover;
        border-radius: 5px;

        @media (max-width: 1023px) {
            height: auto;
            width: 100%;
        }
    }

    .swiper-container {
        height: 100%;
    }

    .swiper-slide {
        display: flex;
        justify-content: center;
        align-items: center;
    }
}