.list_section {
    padding: 154px 0;

    @media (max-width: 1023px) {
        padding: 60px 0;
    }

    .section_title {
        margin-bottom: 60px;
    }
}

.list_section__block {

    @media (min-width: 1300px) {
        display: flex;
        flex-direction: column;
        align-content: space-between;
        flex-wrap: wrap;
        height: 232px;
        padding: 0 31px;
    }
}

.list_section__item {
    display: flex;
    align-items: center;
    margin-bottom: 16px;
    font-size: 18px;
    line-height: 21px;
    letter-spacing: -0.015em;

    @media (max-width: 767px) {
        font-size: 16px;
        line-height: 18px;
    }

    &:before {
        display: flex;
        justify-content: center;
        align-items: center;
        flex-shrink: 0;
        width: 46px;
        height: 46px;
        margin-right: 32px;
        border-radius: 50%;
        background: #E1E4FF;
        font-weight: bold;
        color: #5A6BFF;

        @media (max-width: 767px) {
            margin-right: 15px;
        }
    }

    @media (min-width: 1300px)  {

        &:nth-child(4) {
            margin-bottom: 0;
        }
        &:last-child {
            margin-bottom: 0;
        }
        &:nth-child(-n+4) {
            margin-right: 73px;
        }
    }
}

@for $i from 1 through 8 {
    .list_section__item:nth-child(#{$i}) {
      
        &:before {
            content: '#{$i}';
        }
    }
  }