.event__block {
    width: 51.2%;
    height: 664px;
    padding-top: 32px;
    padding-right: 18px;
    overflow: auto;

    @media (max-width: 1023px) {
       width: 100%;
       padding-right: 0;
    }

    @media (min-width: 1024px) {

        &::-webkit-scrollbar {
            width: 14px;
            background: #f9f9f9;
        }
    
        &::-webkit-scrollbar-button {
            display: none;
        }
    
        &::-webkit-scrollbar-thumb {   
            border: 4px solid #f9f9f9;
            width: 6px;
            background: #eee;
            border-radius: 26px;
        }
    
    }
    .section_title {
        margin-bottom: 16px;
    }
}